.App {
	text-align: center;
}

.App-logo {
	height: 40vmin;
	pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
	.App-logo {
		animation: App-logo-spin infinite 20s linear;
	}
}

.App-header {
	background-color: #282c34;
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	font-size: calc(10px + 2vmin);
	color: white;
}

.App-link {
	color: #61dafb;
}

@keyframes App-logo-spin {
	from {
		transform: rotate(0deg);
	}
	to {
		transform: rotate(360deg);
	}
}

.h_iframe-aparat_embed_frame {
	position: relative;
}
.h_iframe-aparat_embed_frame .ratio {
	display: block;
	width: 100%;
	height: auto;
}
.h_iframe-aparat_embed_frame iframe {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
}

.banner {
	background: #f5f5f5;
	width: 1200px;
	margin: 0 auto;
	padding: 20px;
	border-radius: 5px;
	margin-top: 30px;
	margin-bottom: 30px;
	direction: rtl;
}

.banner a {
	display: block;
	text-decoration: none;
	color: #b0597b;
	font-size: 20px;
	font-weight: bold;
	margin-bottom: 20px;
}

.banner strong {
	color: #222;
	font-size: 14px;
	text-decoration: underline;
}
